.kyra__pink-title{
    display: flex;
}

.kyra__pink-filler{
    position: relative;
}
.kyra__pink-title h1{
    color: black;
    font-size: 50px;
    border-bottom: 5px solid black;
    z-index: 1;
    position: relative;
    cursor: pointer;
}

.kyra__pink-filler:hover .filler{
    background-color: var(--color-main);
    width: 100%;
    height: 50%;
    position: absolute;
    bottom: 0;
    transition: 0.3s ease;
}

@media screen and (max-width:1500px) {
    .kyra__pink-title h1{
        font-size: 45px;
    }
}
@media screen and (max-width:1000px) {
    .kyra__pink-title h1{
        font-size: 40px;
    }
}
@media screen and (max-width:450px) {
    .kyra__pink-title h1{
        font-size: 38px;
    }
}
