.section__padding {
    padding: 6rem 8rem;
  }

.flex__center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex__wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

@media screen and (max-width:1300px) {
  .section__padding {
    padding: 5rem 5rem;
  }
}

@media screen and (max-width:900px) {
  .section__padding {
    padding: 4rem 4rem;
  }
}

@media screen and (max-width:450px) {
  .section__padding {
    padding: 3rem 3rem;
  }
}

