.kyra__heading{
    width: 100%;
    height: 85vh;
    display: flex;
    align-items: center;
}

.kyra__heading-content{
    flex:1;
    margin-right: 1rem;
}
.kyra__heading-image{
    flex:1;
    margin-left: 1rem
}
.kyra__heading-image img{
    width: 100%;
    height: 100%;
}
.kyra__heading-content h1{
    font-size: 5rem;
    margin: 0;
}
.number-container p:first-child{
    font-size: 2rem;
    margin: 0;
}
.kyra__heading-content p:last-child{
    font-size: 3rem;
    margin-bottom: 2rem;
    margin-top: 2rem;
}


.kyra__heading-content button{
    width: 150px;
    height: 60px;
    border-radius: 10px;
    color: var(--color-main);
    background-color: var(--color-white);
    font-size: 1.3rem;
    cursor: pointer;
    font-weight: 600;
    border: 3px solid var(--color-main);
}

.kyra__heading-content button:hover{
    background-color: var(--color-main);
    color: var(--color-white) ;

}


@media screen and (max-width:1500px) {
    .kyra__heading{

        height: auto;
    }

    .kyra__heading-content h1{
        font-size: 3.75rem;
        margin-top: 1rem;
    }

    .kyra__heading-content p:first-child{
        font-size: 1.35rem
    }

   .kyra__heading-content p:last-child{
        font-size: 2.25rem
    }
    
}


@media screen and (max-width:1000px) {
    .kyra__heading{
        flex-direction: column;
    }

    .kyra__heading-image{
        order: 1;
        width: 100%;
        margin: 0;
    }

    .kyra__heading-content{
        order: 2;
    }
    
}

@media screen and (max-width:450px) {

    .kyra__heading-content button{
        width: 100px;
        height: 30px;
        font-size: 0.8rem;
    }


    .kyra__heading-content h1{
        font-size: 2rem;
        margin-top: 1rem;
    }

    .kyra__heading-content p:first-child{
        font-size: 0.75rem
    }

   .kyra__heading-content p:last-child{
        font-size: 1rem
    }
    
}