.kyra__testimonial{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
}
.kyra__testi{
    display: flex;
    margin-top: 3rem;
}

.testi{
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    padding: 40px;
    flex: 1;
    margin: 40px;
}

.testi h3{
    font-size: 2rem;
    margin-bottom: 1rem;
    color: black;
}

.testi p{
    font-size: 1.5rem;
    margin-top: 1rem;
    color: black;
}

#testi1:hover, #testi2:hover , #testi3:hover{
    cursor: pointer;
    transform: translateY(-30px);
}

@media screen and (max-width:1500px){
    .kyra__testi{
        flex-direction: column;
        margin-top: 3rem;
    }
}

@media screen and (max-width:700px){
    .testi h3{
        font-size: 1.5rem;
        margin-bottom: 1rem;
        color: black;
    }
    
    .testi p{
        font-size: 1rem;
        margin-top: 1rem;
        color: black;
    }
}