.kyra__ingredients{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    padding: 30px 100px;
    position: relative;
}

.kyra__ingredients img{
    margin-top: 50px;
    width: 100px;
    height: 100px;
    margin-bottom: 50px;
}

.kyra__ingredients h2{
    font-size: 30px;
}
.kyra__ingredients p{
    font-size: 25px;
    text-align: center;
}

.kyra__ingredients img:last-child{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    opacity: 1;
}

@media screen and (max-width:1500px) {
    .kyra__ingredients{
        padding: 25px 45px;
    }
}

@media screen and (max-width:1000px) {
    .kyra__ingredients h2{
        font-size: 25px;
    }
    .kyra__ingredients p{
        font-size: 20px;
        text-align: center;
    }

    .kyra__ingredients{
        padding: 25px 40px;
    }
}

@media screen and (max-width:400px) {
    .kyra__ingredients h2{
        font-size: 22px;
    }
    .kyra__ingredients p{
        font-size: 16px;
        text-align: center;
    }

    .kyra__ingredients{
        padding: 10px 20px;
    }
}