.kyra__lipbalm{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 85vh;
}

.kyra__lipbalm-container{
    display: flex;
    width: 100%;
    height: 100%;
    margin-top: 4rem;
}

.kyra__lipbalm-image{
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}   
.kyra__lipbalm-image__title h1,.kyra__lipbalm-content h1:first-child {
    font-size: 40px;
}   
.kyra__lipbalm-image__title h3{
    font-size: 20px;
}   

.kyra__lipbalm-content{
    flex: 1.5;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.kyra__lipbalm-content__text{
    width: 100%;
    height: 650px;
}
.kyra__lipbalm-image__img{
    width: 650px;
    height: 650px;
    position: relative;
}   

.kyra__lipbalm-image__img img{
    width: 100%;
    height: 100%;
}   
.kyra__lipbalm-image__arrow{
    display: flex;
    position: absolute;
    bottom: -8%;
    width: 150px;
    justify-content: space-between;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
}

@media screen and (max-width:1500px) {
    .kyra__lipbalm{
        height: auto;
    }

    .kyra__lipbalm-container{
        flex-direction: column;
        align-items: center;
    }

    .kyra__lipbalm-content{
        margin-top: 4rem;
    }

    .kyra__lipbalm-content__text{
        width: 100%;
        height: 650px;
    }
    .kyra__lipbalm-image__img{
        width: 550px;
        height: 550px;
    }  
    
}


@media screen and (max-width:1000px) {

    .kyra__lipbalm-image__img{
        width: 400px;
        height: 400px;
    }  

    .kyra__lipbalm-content__text{
        width: 100%;
        height: 650px;
    }

    .kyra__lipbalm-content__text { 
        margin-top: 2rem;
    }
}

@media screen and (max-width:450px) {
    .kyra__lipbalm-image__img{
        width: 100%;
        height: auto;
    }  
    
}