.kyra__liprescue{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 85vh;
}

.kyra__liprescue-container{
    display: flex;
    margin-top: 4rem;
    width: 100%;
    justify-content: space-between;
}

.kyra__liprescue-content__lelf{
    width: 550px;
    height: auto;
    padding: 60px;
    background-color: var(--color-purple);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.kyra__liprescue-content__right{
    background-color: var(--color-green);
    width: 550px;
    height: auto;
    padding: 60px;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.kyra__liprescue-content__video{
    width: 400px;
    height: auto;
    background-color: black;
}    
.kyra__liprescue-content__video video{
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.9;
}    

.kyra__liprescue-content__lelf h1, .kyra__liprescue-content__right h1{
    font-size: 30px;
}

.kyra__liprescue-content__lelf ul li, .kyra__liprescue-content__right ul li{
    font-size: 25px;
    line-height: 60px;
    list-style: none;
    text-align: center;
}


.kyra__liprescue-container img{
    width: 100px;
    height: 100px;
    margin-top: 3rem;
    margin-bottom: 3rem;
}



@media screen and (max-width:1800px) {
    .kyra__liprescue{
        height: auto;
    }
    .kyra__liprescue-content__lelf{
        width: 400px;
        padding: 30px;
    }
    
    .kyra__liprescue-content__video{
        width: 300px;
        background-color: black;
    }    

    .kyra__liprescue-content__right{
        width: 400px;
        padding: 30px;
    }

    .kyra__liprescue-content__lelf h1, .kyra__liprescue-content__right h1{
        font-size: 25px;
    }

    .kyra__liprescue-content__lelf ul li, .kyra__liprescue-content__right ul li{
        font-size: 20px;
        line-height: 40px;
    }
}

@media screen and (max-width:1400px){
    .kyra__liprescue-content__lelf{
        width: 300px;
        padding: 25px;
    }
    
    .kyra__liprescue-content__video{
        width: 200px;
        background-color: black;
    }    

    .kyra__liprescue-content__right{
        width: 300px;
        padding: 25px;
    }

    .kyra__liprescue-content__lelf h1, .kyra__liprescue-content__right h1{
        font-size: 25px;
    }

    .kyra__liprescue-content__lelf ul li, .kyra__liprescue-content__right ul li{
        font-size: 20px;
        line-height: 40px;
    }
}

@media screen and (max-width:1000px){
    .kyra__liprescue-container{
        flex-direction: column;
        align-items: center;
    }
    .kyra__liprescue-content__lelf{
        width: 75%;
        padding: 40px;
        margin-bottom: 2rem;
    }
    
    .kyra__liprescue-content__video{
        width: 75%;
        background-color: black;
        margin-bottom: 2rem;
    }    

    .kyra__liprescue-content__right{
        width: 75%;
        padding: 40px;
    }

    .kyra__liprescue-content__lelf h1, .kyra__liprescue-content__right h1{
        font-size: 25px;
    }

    .kyra__liprescue-content__lelf ul li, .kyra__liprescue-content__right ul li{
        font-size: 20px;
        line-height: 40px;
    }
}