.kyra__navbar{
    display: flex;
    justify-content: space-between;
    padding: 2rem 8rem;
    background-color: var(--color-main);
    align-items: center;
    z-index: 1001;
}

.kyra__navbar-links{
    display: flex;
    align-items: center;
}
.kyra__navbar-links p{ 
    font-size: 1.68rem;
    margin: 0 2rem;
    cursor: pointer;
}
.kyra__navbar-links p:hover{ 
    color: white;
    font-size: 2rem;
}

.kyra__navbar h1{
    font-size: 2.618rem;
    color: white;
    cursor: pointer;
}

.kyra__navbar button{
    width: 150px;
    height: 60px;
    border-radius: 10px;
    color: white;
    background-color: var(--color-main);
    font-size: 1.3rem;
    cursor: pointer;
    font-weight: 600;
    border: 2px solid white;
}

.kyra__navbar button:hover{
    color:var(--color-main); 
    background-color:white;
    transition: 0.5s ease;
}

.kyra__navbar-mobile{
    display: none;
}

.kyra__navbar-mobile_overlay{
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    background-color: var(--color-main);
    position: fixed;
    padding: 2rem 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1000 ;
    transition: 0.5s ease;

}



@media screen and (max-width:1300px) {
    .kyra__navbar{
        padding: 2rem 4rem;
    }
    .kyra__navbar-links{
        display: none ;
    }
    .kyra__navbar-mobile{
        display: flex;
    }
    .kyra__navbar button{
        display: none;
    }
    .overlay_close{
        margin-left: auto;
        margin-bottom: 4rem;
        color: white;
    }
    .kyra__navbar-mobile_links p{
        font-size: 3rem;
        color: white;
        margin-bottom: 2rem;
    }
    .kyra__navbar-mobile_links button{
        display: block;
        margin-top: 4rem;
    }
}