@import url("https://fonts.googleapis.com/css2?family=Inter:wght@600;700&family=Manrope:wght@300;400&display=swap");

:root {
  --font-base: 'Inter', sans-serif;
  --font-alt: 'Manrope', sans-serif;

  --color-white: #F8F6F1;
  --color-main:#FFC2CF;
  --color-pink:#FFD6DF;
  --color-purple:#CCCCFF;
  --color-green: #BCF1BC;
  --color-black:#000F08;
  --color-text:#37123C;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  font-family: var(--font-alt);

}

a {
  color: unset;
  text-decoration: none;
}

body{
  background: var(--color-white);
  color: #37123C;
}

@keyframes fade-up{
  0%{
    opacity: 0;
    transform: translateY(30px) scale(0.9);
  }

  100%{
    opacity: 1;
    transform: translateY(0px) scale(1);
  }
}