.kyra__lipbalm-feature{
    width: 100%;
    height: 180px;
    background-color: var(--color-pink);
    display: flex;
    margin-bottom: 55px;
    padding: 30px;
    border-radius: 10px;
    align-items: center;
    justify-content: center;
}

.kyra__lipbalm-feature__icon{
    flex-basis:20%;
    width: 120px;
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right:30px;
}
.kyra__lipbalm-feature__icon h1{
    font-size: 30px;
}
.kyra__lipbalm-feature__icon img{
    width: 80px;
    height: 80px;
}

.kyra__lipbalm-feature__text{
    flex-basis: 80%;
    padding: 20px;
    font-size: 25px;
    display: flex;
    align-items: center;
}


@media screen and (max-width:1000px) {
    .kyra__lipbalm-feature__icon h1{
        font-size: 25px;
    } 
    .kyra__lipbalm-feature__text{
        padding: 20px;
        font-size: 20px;
    }
    .kyra__lipbalm-feature__icon img{
        width: 60px;
        height: 60px;
    }
}

@media screen and (max-width:700px) {
    .kyra__lipbalm-feature__icon h1{
        font-size: 22.5px;
    } 
    .kyra__lipbalm-feature__text{
        padding: 20px;
        font-size: 18px;
    }
    .kyra__lipbalm-feature__icon img{
        width: 60px;
        height: 60px;
    }

    .kyra__lipbalm-feature__icon img{
        width: 45px;
        height: 45px;
    }
}

@media screen and (max-width:450px) {
    .kyra__lipbalm-feature{
        padding: 10px;
    }
    .kyra__lipbalm-feature__icon h1{
        font-size: 16px;
    } 
    .kyra__lipbalm-feature__text{
        padding: 10px;
        font-size: 14px;
    }
    .kyra__lipbalm-feature__icon img{
        width: 35px;
        height: 35px;
    }
}


