.kyra__ingredient{
    width: 100%;
    height: 90vh;
}

.kyra__ingredient-container{
    margin-top: 4rem;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 4rem;
}

.kyra__ingredient-content{
    width: 100%;
    height: 400px;
    border: 5px solid #E3DDCA;
    position: relative;
}
.kyra__ingredient-content img:last-child{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0;
}

#a1:hover img,
#a2:hover img,
#a3:hover img,
#a4:hover img,
#a5:hover img,
#a6:hover img{
    opacity: 0;
}

#a1:hover img:last-child,
#a2:hover img:last-child,
#a3:hover img:last-child,
#a4:hover img:last-child,
#a5:hover img:last-child,
#a6:hover img:last-child{
    opacity: 0.8;
}

#a1:hover ,
#a2:hover ,
#a3:hover ,
#a4:hover ,
#a5:hover ,
#a6:hover {
cursor: pointer;
color: white;
}

@media screen and (max-width:1800px) {
    .kyra__ingredient-content{
        height: 500px;
    }

    .kyra__ingredient{

        height: auto;
    }
    
}

@media screen and (max-width:1400px) {
    .kyra__ingredient-container{
        grid-template-columns: repeat(2,1fr);
        gap: 2rem;
    }
}

@media screen and (max-width:750px) {
    .kyra__ingredient-container{
        grid-template-columns: repeat(1,1fr);
        gap: 2rem;
    }
}