.kyra__lipbalm-image__img{
    background-color: black;
}

.kyra__lipbalm-image__img img:hover{
    opacity: 0.7;
    cursor: pointer;
}

.kyra__lipbalm-image__img{
    position: relative;
}

.shopee{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    opacity: 0;
}

.kyra__lipbalm-image__img img:hover + .shopee{
    opacity: 1;
}

